* {
  box-sizing: border-box;
}

html {
  font-family: OpenSans, sans-serif;
  font-size: 16px;
  scroll-behavior: smooth;
}

body {
  overflow-x: hidden;
  position: relative;
  margin: 0;
  min-height: 100vh;
  color: $mainColor;
  font-family: poppins-light;

  &.no-scroll {
    overflow: hidden;
  }
}

// img {
//   max-width: 100%;
// }

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  font-weight: normal;
  padding: 0;
}

a {
  text-decoration: none;
  color: inherit;
  &:hover {
    text-decoration: underline;
  }
}

button {
  font-family: inherit;
  cursor: pointer;

  * {
    pointer-events: none;
  }
}

input {
  font-family: inherit;
  text-align: inherit;
}

.loader {
  margin-inline-start: 50%;
  // translate: -50%;
  position: fixed;
  top: 50%;
  right: 50%;
  transform: translate(50%, -50%);
}

.alert {
  position: fixed;
  top: 100px;
  left: 50%;
  width: 200px;
  height: 100px;
  background-color: red;
}

// MAC like scrollbar
::-webkit-scrollbar {
  width: 8px;
  background-color: rgba(0, 0, 0, 0);
  -webkit-border-radius: 100px;
}

::-webkit-scrollbar:hover {
  background-color: rgba(0, 0, 0, 0.09);
}

::-webkit-scrollbar-thumb:vertical {
  background: rgba(0, 0, 0, 0.5);
  -webkit-border-radius: 100px;
}

::-webkit-scrollbar-thumb:vertical:active {
  -webkit-border-radius: 100px;
}
